















import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class BannerHeading extends Vue {
  @Prop() public readonly name!: string;
  @Prop() public readonly id?: string;
  @Prop() public readonly bannerImageUrl?: string;

  private _animatedIn: boolean;

  constructor() {
    super();
    this._animatedIn = false;
  }

  public setHeadingVisible(setVisible: boolean) {
    const isHidden: boolean = this.$el.classList.contains("heading-hidden");

    if (setVisible) {
      this.$el.classList.remove("heading-hidden");
    } else if (!isHidden) {
      this.$el.classList.add("heading-hidden");
    }
  }

  public animateIn(): void {
    if (!this.$el.classList.contains("animate-in")) {
      this.$el.classList.add("animate-in");
      this._animatedIn = true;
    }
  }

  public isVisible(): boolean {
    let visible: boolean = true;

    if (!this._animatedIn) {
      const boundingRect: DOMRect = this.$el.getBoundingClientRect();
      const screenPosition: number = boundingRect.top + window.scrollY - 300;

      visible = window.scrollY > screenPosition;
    }

    return visible;
  }
}
