





































import { Component, Vue } from "vue-property-decorator";

import MainMenu from "@/components/MainMenu.vue";
import BannerHeading from "@/components/BannerHeading.vue";

import HeroSection from "@/components/page_sections/HeroSection.vue";
import WhatIMadeSection from "@/components/page_sections/WhatIMadeSection.vue";
import HowIMakeItSection from "@/components/page_sections/HowIMakeItSection.vue";
import WhereILearnedSection from "@/components/page_sections/WhereILearnedSection.vue";
import FooterSection from "@/components/page_sections/FooterSection.vue";

@Component({
  components: {
    MainMenu,
    BannerHeading,
    HeroSection,
    WhatIMadeSection,
    HowIMakeItSection,
    WhereILearnedSection,
    FooterSection,
  },
})
export default class App extends Vue {
  private _bannerHeadings!: BannerHeading[];

  constructor() {
    super();

    //Switch to dark theme when the user's current time is in the evening or early morning.
    const hourOfDay: number = new Date().getHours();

    if (hourOfDay <= 6 || hourOfDay >= 18) {
      document.body.setAttribute("theme", "dark");
    }
  }

  private mounted() {
    this._bannerHeadings = this.$children.filter(
      (c) => c instanceof BannerHeading
    ) as BannerHeading[];
    this.setup();
  }

  private setup() {
    if (!App.isMobile) {
      for (let i = 0; i < this._bannerHeadings.length; i++) {
        this._bannerHeadings[i].setHeadingVisible(false);
      }

      window.addEventListener("scroll", this.animateBannerHeadings);
      this.animateBannerHeadings();
    }
  }

  private animateBannerHeadings() {
    for (let i = 0; i < this._bannerHeadings.length; i++) {
      if (this._bannerHeadings[i].isVisible()) {
        this._bannerHeadings[i].animateIn();
        this._bannerHeadings.splice(i, 1);

        if (this._bannerHeadings.length == 0) {
          window.removeEventListener("scroll", this.animateBannerHeadings);
        }
      }
    }
  }

  public static get isMobile(): boolean {
    return window.outerWidth <= 1024 || window.outerHeight <= 1024;
  }

  public static get browserIsLegacyEdge(): boolean {
    return /Edge/.test(navigator.userAgent);
  }
}
