




import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import App from "@/App.vue";

@Component
export default class ScrollElementButton extends Vue {
  @Prop() public readonly text?: string;
  @Prop() public readonly scrollElementId?: string;
  @Prop() public readonly viewOffset?: number;
  @Prop() public readonly scrollOffset?: number;

  private _scrollElement?: HTMLElement | null;

  private mounted() {
    if (this.scrollElementId) {
      this._scrollElement = document.getElementById(this.scrollElementId);
    }
  }

  public sectionInView(): boolean {
    let result: boolean = false;

    if (this._scrollElement) {
      const rect: DOMRect = this._scrollElement.getBoundingClientRect();
      const offset: number = this.viewOffset ? this.viewOffset : 1;
      result = rect.top < offset && rect.bottom > offset;
    }

    return result;
  }

  private onClick() {
    this.$emit("click", this);
    this.scrollToSection();
  }

  public scrollToSection() {
    if (this._scrollElement) {
      let scrollPosition: number =
        this._scrollElement.getBoundingClientRect().top + scrollY;

        if(this.scrollOffset != null && !Number.isNaN(this.scrollOffset)) {
          scrollPosition += Number(this.scrollOffset);
        }

      if (App.browserIsLegacyEdge) {
        window.scroll(window.scrollX, scrollPosition);
      } else {
        scrollTo({
          top: scrollPosition,
          behavior: "smooth"
        });
      }
    } else {
      console.warn(
        `ScrollElementButton cannot scroll because the assigned "scrollElementId" property is null or does not exist on the document.`
      );
    }
  }

  public setHighlighted(highlighted: boolean) {
    if (highlighted) {
      if (!this.$el.classList.contains("active-section")) {
        this.$el.classList.add("active-section");
      }
    } else {
      this.$el.classList.remove("active-section");
    }
  }
}
