







import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class WordRoller extends Vue {
  @Prop() public readonly prefix?: string;
  @Prop() public readonly words!: string[];

  private _wordRollSpan!: HTMLSpanElement;
  private _wordIndex!: number;

  private mounted() {
    if (this.words != null && this.words.length > 0) {
      this._wordRollSpan = this.$refs["word-roll"] as HTMLSpanElement;
      this._wordIndex = 0;

      this.updateWordWoll();
      setTimeout(() => setInterval(this.updateWordWoll, 2000), 225);
    }
  }

  private updateWordWoll() {
    this._wordRollSpan.innerText = this.words[this._wordIndex];

    this._wordIndex++;
    if (this._wordIndex >= this.words.length) {
      this._wordIndex = 0;
    }
  }
}
