

























import Vue from "vue";
import Component from "vue-class-component";
import WordRoller from "@/components/WordRoller.vue";
import UIButton from "@/components/ui/UIButton.vue";
import App from "@/App.vue";

@Component({
  components: {
    WordRoller,
    UIButton
  }
})
export default class HeroSection extends Vue {
  private scrollToEnd() {
    const sectionHeight: number = this.$el.getBoundingClientRect().height;

    if (App.browserIsLegacyEdge) {
      window.scroll(window.scrollX, sectionHeight);
    } else {
      scrollTo({
        top: sectionHeight,
        behavior: "smooth"
      });
    }
  }
}
