






















import Vue from "vue";
import Component from "vue-class-component";
import UIButton from "@/components/ui/UIButton.vue";
import { Prop } from "vue-property-decorator";

@Component({ components: { UIButton } })
export default class ExpandableContent extends Vue {
  @Prop() public readonly name?: string;
  @Prop() public readonly group?: string;

  private _groupContents?: HTMLDivElement[];

  private mounted() {
    if (this.group) {
      this._groupContents = Array.from(
        document.querySelectorAll(`.expandable-content[group='${this.group}']`)
      ) as HTMLDivElement[];
    }
  }

  public toggleExpanded() {
    this.closeOthersInGroup();
    this.$el.classList.toggle("opened");
  }

  private closeOthersInGroup() {
    if (this._groupContents) {
      for (const expandableContent of this._groupContents) {
        if (expandableContent != this.$el) {
          expandableContent.classList.remove("opened");
        }
      }
    }
  }
}
