






































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import IconChip from "@/components/IconChip.vue";
import UIButton from "@/components/ui/UIButton.vue";

import Image from "@/interfaces/Image";
import TechData from "@/interfaces/TechData";
import IconChipData from "@/interfaces/IconChipData";
import AnchorLink from "@/interfaces/AnchorLink";

@Component({
  components: {
    IconChip,
    UIButton
  }
})
export default class TechShowcase extends Vue implements TechData {
  @Prop() public readonly name!: string;
  @Prop() public readonly description!: string;
  @Prop() public readonly image!: Image;
  @Prop() public readonly link?: AnchorLink;
  @Prop() public readonly chips?: IconChipData[];
}
