












































import Vue from "vue";
import Component from "vue-class-component";
import ScrollElementButton from "@/components/ui/ScrollElementButton.vue";
import App from "@/App.vue";

@Component({ components: { ScrollElementButton } })
export default class MainMenu extends Vue {
  private _scrollElementButtons!: ScrollElementButton[];

  private mounted() {
    this._scrollElementButtons = this.$children.filter(
      (c) => c instanceof ScrollElementButton
    ) as ScrollElementButton[];

    for (let i = 0; i < this._scrollElementButtons.length; i++) {
      this._scrollElementButtons[i].$on("click", this.onMenuButtonClicked);
    }

    if (!App.isMobile) {
      window.addEventListener("scroll", this.updateCurrentPageSection);
    }
  }

  private updateCurrentPageSection() {
    for (const button of this._scrollElementButtons) {
      button.setHighlighted(button.sectionInView());
    }
  }

  private toggleMobileMenu() {
    this.$el.classList.toggle("mobile-expanded");
    this.updateCurrentPageSection();
  }

  private onMenuButtonClicked(menuButton: ScrollElementButton) {
    if (App.isMobile) {
      for (const button of this._scrollElementButtons) {
        button.setHighlighted(button == menuButton);
      }
    }
  }
}
